/* leaf animations */

#leaves {
    position: absolute;
    top: 0%;
    width: 100%;
    margin-right: -50%;
    text-align: right;
}

#leaves i {
    display: inline-block;
    width: 250px;
    height: 100%;
    background: linear-gradient(to bottom right, #eaaf77, #f9e260);
    transform: skew(20deg);
    border-radius: 5% 40% 70%;
    box-shadow: inset 0px 0px 1px #222;
    border: 1px solid #333;
    z-index: 1;
    animation: falling 5s 0s infinite;
}

#leaves i:nth-of-type(2n) { animation: falling2 5s 0s infinite; }
#leaves i:nth-of-type(3n) { animation: falling3 5s 0s infinite; }

#leaves i:before {
  position: absolute;
  content: '';
  right: 9px;
  height: 27px;
  width: 32px;
  transform: rotate(49deg);
  border-radius: 0% 15% 15% 0%;
  border-top: 1px solid #222;
  border-bottom: 1px solid #222;
  border-left: 0px solid #222;
  border-right: 1px solid #222;
  background: linear-gradient(to right, rgb(251, 184, 113, 1), #fde79e);
  z-index: 1;
}

#leaves i:after {
  content: '';
  height: 125px;
  width: 10px;
  background: linear-gradient(to right, rgba(0,0,0,.15), rgba(0,0,0,0));
  display: block;
  transform: rotate(125deg);
  position: absolute;
  left: 85px;
  border-radius:50%;
}

/* Big leaf */
#leaves i:nth-of-type(n)    { height: 35px; width:45px; }
#leaves i:nth-of-type(n):before { width:7px; height:5px; top:17px; right:1px; }
#leaves i:nth-of-type(n):after { width:2px; height:17px; left: 12px; top:0px; }

/* Big leaf */
#leaves i:nth-of-type(2n+1)    { height:23px; width:25px; }
#leaves i:nth-of-type(2n+1):before { width:4px; height:3px; top:7px; right:0px; }
#leaves i:nth-of-type(2n+1):after { width:2px; height:6px; left: 5px; top:1px; }

#leaves i:nth-of-type(3n+2)  { height:17px; width:23px; }
#leaves i:nth-of-type(3n+2):before  { height:4px; width:4px; top:12px; right:1px; }
#leaves i:nth-of-type(3n+2):after  { height:10px; width:2px; top:1px; left:8px; }

#leaves i:nth-of-type(n)   { animation-delay: 2.9s;}
#leaves i:nth-of-type(2n)  { animation-delay: 4.9s;}
#leaves i:nth-of-type(3n)  { animation-delay: 3.3s;}
#leaves i:nth-of-type(4n)  { animation-delay: 5.4s;}
#leaves i:nth-of-type(5n)  { animation-delay: 6s;  }
#leaves i:nth-of-type(6n)  { animation-delay: 4.5s;}
#leaves i:nth-of-type(7n)  { animation-delay: 3.8s;}
#leaves i:nth-of-type(8n)  { animation-delay: 2.5s;}
#leaves i:nth-of-type(9n)  { animation-delay: 4.3s;}
#leaves i:nth-of-type(10n) { animation-delay: 3.5s;}
#leaves i:nth-of-type(11n) { animation-delay: 2.2s;}
#leaves i:nth-of-type(12n) { animation-delay: 5.1s;}
#leaves i:nth-of-type(13n) { animation-delay: 2s;  }
#leaves i:nth-of-type(14n) { animation-delay: 5.7s;}
#leaves i:nth-of-type(15n) { animation-delay: 4s;  }

#leaves i:nth-of-type(n)    { background: linear-gradient(to bottom right, #fced71, #ac9c86); }
#leaves i:nth-of-type(2n+2)  { background: linear-gradient(to bottom right, #fdcf6a, #f9ee54); }
#leaves i:nth-of-type(4n+1)  { background: linear-gradient(to bottom right, #f7cf8b, #7d2436); }

#leaves i:nth-of-type(n)    { opacity: .7;}
#leaves i:nth-of-type(3n+1)  { opacity: .5;}
#leaves i:nth-of-type(3n+2)  { opacity: .3;}

#leaves i:nth-of-type(n)    {transform: rotate(180deg);}


#leaves i:nth-of-type(n) { animation-timing-function:ease-in-out;}

@keyframes falling {
    
    0% {
        -webkit-transform:
            translate3d(300,0,0)
            rotate(0deg);
    }
    
    100% {
        -webkit-transform:
            translate3d(-350px,700px,0)
            rotate(90deg);
        opacity: 0;
    }
}

@keyframes falling3 {
     0% {
        -webkit-transform:
            translate3d(0,0,0)
            rotate(-20deg);
    }
    
    100% {
        -webkit-transform:
            translate3d(-230px,640px,0)
            rotate(-70deg);
        opacity: 0;
    }
}

@keyframes falling2 {
     0% {
        -webkit-transform:
            translate3d(0,0,0)
            rotate(90deg);
    }
    
    100% {
        -webkit-transform:
            translate3d(-400px,680px,0)
            rotate(0deg);
        opacity: 0;
    }
}